import React, { Component } from "react";
import { connect } from "react-redux";
import { Field, reduxForm } from "redux-form";
import Swal from "sweetalert2";
import validationRosok from "../../Validations/validationRosok";
import { renderField, renderFieldSelect } from "../../renderField";
import { AxiosMasterGet } from "../../Axios";
import { SkeletonLoading } from "../../GlobalComponent/function";
import { setCameraURI } from "../../actions/cameraAction";
import CameraPhoto, { FACING_MODES } from "jslib-html5-camera-photo";
import { dataURLtoFile, PlaceHolder, resizeFile } from "../../helper";
import { CircularProgressbar } from "react-circular-progressbar";

const maptoState = (state) => {
  return {
    initialValues: {
      kodeJenis: state.provinsi.EditModel.kodeJenis,
      kodeKategori: state.provinsi.EditModel.kodeKategori,
      namaJenis: state.provinsi.EditModel.namaJenis,
      kadarBeli: state.provinsi.EditModel.kadarBeli,
      kadarJual: state.provinsi.EditModel.kadarJual,
      kadarCetak: state.provinsi.EditModel.kadarCetak,
      kode_intern: state.provinsi.EditModel.kodeIntern,
    },
    image: state.provinsi.EditModel.image,
    onSend: state.provinsi.onSend,
  };
};

class FormKodeBarangEdit extends Component {
  constructor(props) {
    super(props);
    this.cameraPhoto = null;
    this.videoRef = React.createRef();
    this.state = {
      response: "",
      listPerhiasan: [],
      listJual: [],
      listBeli: [],
      listKategori: [],
      dataUri: PlaceHolder,
      prevCamera: "col-lg-12 d-none",
      hasilCamera: "col-lg-12 d-none",
      imageEdit: "col-lg-12",
    };
  }

  componentDidMount() {
    // SET CAMERA
    this.cameraPhoto = new CameraPhoto(this.videoRef.current);
    setTimeout(() => {
      this.cameraPhoto.startCamera(FACING_MODES.ENVIRONMENT, {
        width: 720,
        height: 720,
      });
    }, 200);
    // SET CAMERA
    AxiosMasterGet("kadar/get/by-type/kadar-cetak")
      .then((res) => {
        this.setState({
          listPerhiasan: res.data,
        });
      })
      .catch((err) =>
        Swal.fire({
          position: "top-end",
          icon: "error",
          title: "Ambil Data Kadar Error",
          showConfirmButton: false,
          timer: 1500,
        })
      );
    AxiosMasterGet("kadar/get/by-type/kadar-beli")
      .then((res) => {
        this.setState({
          listBeli: res.data,
        });
      })
      .catch((err) =>
        Swal.fire({
          position: "top-end",
          icon: "error",
          title: "Ambil Data Kadar Error",
          showConfirmButton: false,
          timer: 1500,
        })
      );
    AxiosMasterGet("kadar/get/by-type/kadar-jual")
      .then((res) => {
        this.setState({
          listJual: res.data,
        });
      })
      .catch((err) =>
        Swal.fire({
          position: "top-end",
          icon: "error",
          title: "Ambil Data Kadar Error",
          showConfirmButton: false,
          timer: 1500,
        })
      );
    AxiosMasterGet("kategori/get/all")
      .then((res) => {
        this.setState({
          listKategori: res.data,
        });
      })
      .catch((err) =>
        Swal.fire({
          position: "top-end",
          icon: "error",
          title: "Ambil Data Kategori Error",
          showConfirmButton: false,
          timer: 1500,
        })
      );
  }
  async takePhoto() {
    const config = {
      sizeFactor: 1,
      imageCompression: 0.1,
      isImageMirror: false,
    };
    const file = dataURLtoFile(this.cameraPhoto.getDataUri(config));
    const res = await resizeFile(file);
    this.setState({
      dataUri: res,
      prevCamera: "col-lg-12 d-none",
      hasilCamera: "col-lg-12",
      imageEdit: "col-lg-12 d-none",
    });
    this.props.dispatch(setCameraURI(res));
  }
  resetPhoto() {
    this.setState({
      prevCamera: "col-lg-12 ",
      hasilCamera: "col-lg-12 d-none",
      imageEdit: "col-lg-12 d-none",
    });
  }

  handleUpload = async (e) => {
    const reader = new FileReader();
    const file = e.target.files[0];
    reader.onload = this.handleReaderLoaded;
    reader.readAsDataURL(file);
    let compressed = await resizeFile(file);
    this.setState({
      dataUri: reader.result,
      prevCamera: "col-lg-12 d-none",
      hasilCamera: "col-lg-12",
      imageEdit: "col-lg-12 d-none",
    });
    console.log(compressed);
    this.props.dispatch(setCameraURI(compressed));
  };

  handleReaderLoaded = (readerEvt) => {
    let binaryString = readerEvt.target.result;
    this.setState({
      imageBase64: btoa(binaryString),
    });
  };
  render() {
    return (
      <>
        {this.props.onSend ? (
          <>
            <div className="row col-lg-12 justify-content-center">
              <div style={{ width: 200, height: 200 }}>
                <CircularProgressbar
                  maxValue={100}
                  value={this.props.percentage}
                  text={`${this.props.percentage}%`}
                ></CircularProgressbar>
              </div>
            </div>
            <div className="row col-lg-12 justify-content-center mt-2">
              <h3>Sedang Mengunggah Data..</h3>
            </div>
          </>
        ) : (
          <form onSubmit={this.props.handleSubmit}>
            <div className="row">
              {/* Awal FIeld */}
              <div className="col-md-3">
                {String(this.state.listKategori) !== "" ? (
                  <div className="form-group">
                    <Field
                      component={renderFieldSelect}
                      label="Kode Jenis"
                      name="kodeKategori"
                      value={this.props.status}
                      normalize={(value) => value && value.toUpperCase()}
                      className="form-control"
                      options={this.state.listKategori.map((obj) => {
                        var payload = {
                          value: obj.kode_kategori,
                          name: obj.nama_kategori,
                        };
                        return payload;
                      })}
                    ></Field>
                  </div>
                ) : (
                  <SkeletonLoading label="Kode Jenis" />
                )}
              </div>
              {/* Akhir Field */}
              {/* Awal Field */}
              <div className="col-md-3">
                <Field
                  name="kodeJenis"
                  type="text"
                  normalize={(value) => value && value.toUpperCase()}
                  component={renderField}
                  label="Kode Model"
                />
              </div>
              {/* Akhir Field */}
              {/* Awal Field */}
              <div className="col-md-6">
                <Field
                  name="namaJenis"
                  type="text"
                  normalize={(value) => value && value.toUpperCase()}
                  component={renderField}
                  label="Nama Model"
                />
              </div>
              {/* Akhir Field */}
              {/* Awal Field */}
              <div className="col-md-3">
                <div className="form-group">
                  {String(this.state.listPerhiasan) !== "" ? (
                    <Field
                      component={renderFieldSelect}
                      label="Cap Kode"
                      name="kadarCetak"
                      value="DEFAULT"
                      className="form-control"
                      options={this.state.listPerhiasan.map((obj) => {
                        var payload = {
                          value: obj.kadar.toString(),
                          name: obj.kadar,
                        };
                        return payload;
                      })}
                    ></Field>
                  ) : (
                    <SkeletonLoading title="Cap Kode" />
                  )}
                </div>
              </div>
              {/* AKhir FIeld */}
              {/* Awal Field */}
              <div className="col-md-3">
                <div className="form-group">
                  {String(this.state.listBeli) !== "" ? (
                    <Field
                      component={renderFieldSelect}
                      label="Kode Awal"
                      name="kadarBeli"
                      value="DEFAULT"
                      className="form-control"
                      options={this.state.listBeli.map((obj) => {
                        var payload = {
                          value: obj.kadar,
                          name: obj.kadar,
                        };
                        return payload;
                      })}
                    ></Field>
                  ) : (
                    <SkeletonLoading title="Kode Awal" />
                  )}
                </div>
              </div>
              {/* AKhir FIeld */}
              {/* Awal Field */}
              {/* <div className="col-md-3">
            <div className="form-group">
              {String(this.state.listJual) !== "" ? (
                <Field
                  component={renderFieldSelect}
                  label="Kode Tukar"
                  name="kadarJual"
                  value="DEFAULT"
                  className="form-control"
                  options={this.state.listJual.map((obj) => {
                    var payload = {
                      value: obj.kadar,
                      name: obj.kadar,
                    };
                    return payload;
                  })}
                ></Field>
              ) : (
                <SkeletonLoading title="Kode Tukar" />
              )}
            </div>
          </div> */}
              {/* AKhir FIeld */}
              <div className="col-lg-3">
                <Field
                  name="kode_intern"
                  component={renderField}
                  type="text"
                  label="Kode Intern"
                  placeholder="Masukan Kode Intern"
                />
              </div>

              <div className="col-lg-12 mb-2">
                <div className="row">
                  <div className="col-lg-4"></div>
                  <div className="col-lg-4 ">
                    <div className={this.state.prevCamera}>
                      <video
                        ref={this.videoRef}
                        autoPlay="true"
                        width="100%"
                        height="100%"
                      />
                    </div>
                    <div className={this.state.imageEdit}>
                      <img
                        src={this.props.image}
                        alt="Gambar Rusak"
                        width="100%"
                        height="100%"
                      />
                    </div>
                    <div className={this.state.hasilCamera}>
                      <img
                        src={this.state.dataUri}
                        alt="Gambar Rusak"
                        width="100%"
                        height="100%"
                      />
                    </div>
                    <div className="text-center mt-2">
                      <button
                        className="btn btn-success"
                        type="button"
                        onClick={() => this.takePhoto()}
                      >
                        Ambil Gambar
                      </button>
                      <button
                        className="btn btn-outline-danger ml-2"
                        type="button"
                        onClick={() => this.resetPhoto()}
                      >
                        Reset Gambar
                      </button>
                    </div>
                  </div>
                  <div className="col-lg-4 my-auto">
                    <h5>Pilih Gambar Dari File : </h5>
                    <div class="input-group mt-2">
                      <div class="input-group-prepend">
                        <button
                          class="btn btn-danger"
                          type="button"
                          onClick={() => this.props.dispatch(setCameraURI(""))}
                        >
                          Hapus
                        </button>
                      </div>
                      <div class="custom-file">
                        <input
                          className="custom-file-input"
                          type="file"
                          name="avatar"
                          id="file"
                          accept=".jpef, .png, .jpg"
                          onChange={(data) => this.handleUpload(data)}
                          src={this.state.image}
                        />
                        <label class="custom-file-label" for="inputGroupFile03">
                          Choose file
                        </label>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
              <div className="col-lg-12 col-md-12 mt-3">
                <div className="text-right">
                  <button
                    type="submit"
                    className="btn btn-info"
                    disabled={this.props.onSend}
                  >
                    {this.props.onSend ? (
                      <span
                        className="spinner-border spinner-border-sm mr-3"
                        aria-hidden="true"
                      ></span>
                    ) : null}
                    Selesai
                <i class="fa fa-paper-plane ml-3" aria-hidden="true"></i>
                  </button>
                </div>
              </div>
            </div>
          </form>
        )}
      </>
    );
  }
}

FormKodeBarangEdit = reduxForm({
  form: "KodeBarangEdit",
  enableReinitialize: true,
  validate: validationRosok,
})(FormKodeBarangEdit);
export default connect(maptoState, null)(FormKodeBarangEdit);
