import React, { Component } from "react";
import { connect } from "react-redux";
import { Field, reduxForm } from "redux-form";
import Swal from "sweetalert2";
import validationRosok from "../../Validations/validationRosok";
import { renderField, renderFieldSelect } from "../../renderField";
import { AxiosMasterGet } from "../../Axios";
import { SkeletonLoading } from "../../GlobalComponent/function";
import { setCameraURI } from "../../actions/cameraAction";
import CameraPhoto, { FACING_MODES } from "jslib-html5-camera-photo";
import { dataURLtoFile, PlaceHolder, resizeFile } from "../../helper";
import { createNumberMask } from "redux-form-input-masks";


const currencyMask = createNumberMask({
  prefix: "Rp. ",
  suffix: ",-",
  locale: "id-ID",
});

const maptoState = (state) => {
  return {
    initialValues: {
      kodeJenis: state.provinsi.EditModel.kodeJenis,
      kodeKategori: state.provinsi.EditModel.kodeKategori,
      namaJenis: state.provinsi.EditModel.namaJenis,
      kadarBeli: state.provinsi.EditModel.kadarBeli,
      kadarJual: state.provinsi.EditModel.kadarJual,
      kadarCetak: state.provinsi.EditModel.kadarCetak,
      kode_intern: state.provinsi.EditModel.kodeIntern,
      harga_jual: state.provinsi.EditModel.harga_jual,
      harga_beli: state.provinsi.EditModel.harga_beli,
    },
    image: state.provinsi.EditModel.image,
    onSend: state.provinsi.onSend,
  };
};

class FormKodeBarangAksesorisEdit extends Component {
  constructor(props) {
    super(props);
    this.cameraPhoto = null;
    this.videoRef = React.createRef();
    this.state = {
      response: "",
      listPerhiasan: [],
      listJual: [],
      listBeli: [],
      listKategori: [],
      dataUri: PlaceHolder,
      prevCamera: "col-lg-12 d-none",
      hasilCamera: "col-lg-12 d-none",
      imageEdit: "col-lg-12",
    };
  }

  componentDidMount() {
    // SET CAMERA
    this.cameraPhoto = new CameraPhoto(this.videoRef.current);
    setTimeout(() => {
      this.cameraPhoto.startCamera(FACING_MODES.ENVIRONMENT, {
        width: 720,
        height: 720,
      });
    }, 200);
    // SET CAMERA
    AxiosMasterGet("kategori/get/all")
      .then((res) => {
        this.setState({
          listKategori: res.data,
        });
      })
      .catch((err) =>
        Swal.fire({
          position: "top-end",
          icon: "error",
          title: "Ambil Data Kategori Error",
          showConfirmButton: false,
          timer: 1500,
        })
      );
  }
  async takePhoto() {
    const config = {
      sizeFactor: 1,
      imageCompression: 0.1,
      isImageMirror: false,
    };
    const file = dataURLtoFile(this.cameraPhoto.getDataUri(config));
    const res = await resizeFile(file);
    this.setState({
      dataUri: res,
      prevCamera: "col-lg-12 d-none",
      hasilCamera: "col-lg-12",
      imageEdit: "col-lg-12 d-none",
    });
    this.props.dispatch(setCameraURI(res));
  }
  resetPhoto() {
    this.setState({
      prevCamera: "col-lg-12 ",
      hasilCamera: "col-lg-12 d-none",
      imageEdit: "col-lg-12 d-none",
    });
  }

  handleUpload = async (e) => {
    const reader = new FileReader();
    const file = e.target.files[0];
    reader.onload = this.handleReaderLoaded;
    reader.readAsDataURL(file);
    let compressed = await resizeFile(file);
    console.log(compressed);
    this.setState({
      dataUri: compressed,
      prevCamera: "col-lg-12 d-none",
      hasilCamera: "col-lg-12",
      imageEdit: "col-lg-12 d-none",
    });
    this.props.dispatch(setCameraURI(compressed));
  };

  handleReaderLoaded = (readerEvt) => {
    let binaryString = readerEvt.target.result;
    this.setState({
      imageBase64: btoa(binaryString),
    });
  };
  render() {
    return (
      <form onSubmit={this.props.handleSubmit}>
        <div className="row">
          {/* Awal FIeld */}
          <div className="col-md-3">
            {String(this.state.listKategori) !== "" ? (
              <div className="form-group">
                <Field
                  component={renderFieldSelect}
                  label="Kode Jenis"
                  name="kodeKategori"
                  value={this.props.status}
                  normalize={(value) => value && value.toUpperCase()}
                  className="form-control"
                  options={this.state.listKategori.map((obj) => {
                    var payload = {
                      value: obj.kode_kategori,
                      name: obj.nama_kategori,
                    };
                    return payload;
                  })}
                ></Field>
              </div>
            ) : (
              <SkeletonLoading label="Kode Jenis" />
            )}
          </div>
          {/* Akhir Field */}
          {/* Awal Field */}
          <div className="col-md-3">
            <Field
              name="kodeJenis"
              type="text"
              normalize={(value) => value && value.toUpperCase()}
              component={renderField}
              label="Kode Model"
            />
          </div>
          {/* Akhir Field */}
          {/* Awal Field */}
          <div className="col-md-6">
            <Field
              name="namaJenis"
              type="text"
              normalize={(value) => value && value.toUpperCase()}
              component={renderField}
              label="Nama Model"
            />
          </div>
          {/* Akhir Field */}
          <div className="col-lg-3">
            <Field
              name="harga_beli"
              component={renderField}
              type="text"
              label="Harga Beli"
              placeholder="Masukan Harga Beli"
              {...currencyMask}
            />
          </div>
          <div className="col-lg-3">
            <Field
              name="harga_jual"
              component={renderField}
              type="text"
              label="Harga Jual"
              placeholder="Masukan Harga Jual"
              {...currencyMask}
            />
          </div>
          
          <div className="col-lg-3">
            <Field
              name="kode_intern"
              component={renderField}
              type="text"
              label="Kode Intern"
              placeholder="Masukan Kode Intern"
            />
          </div>

          <div className="col-lg-12 mb-2">
            <div className="row">
              <div className="col-lg-4"></div>
              <div className="col-lg-4 ">
                <div className={this.state.prevCamera}>
                  <video
                    ref={this.videoRef}
                    autoPlay="true"
                    width="100%"
                    height="100%"
                  />
                </div>
                <div className={this.state.imageEdit}>
                  <img
                    src={this.props.image}
                    alt="Gambar Rusak"
                    width="100%"
                    height="100%"
                  />
                </div>
                <div className={this.state.hasilCamera}>
                  <img
                    src={this.state.dataUri}
                    alt="Gambar Rusak hasil"
                    width="100%"
                    height="100%"
                  />
                </div>
                <div className="text-center mt-2">
                  <button
                    className="btn btn-success"
                    type="button"
                    onClick={() => this.takePhoto()}
                  >
                    Ambil Gambar
                  </button>
                  <button
                    className="btn btn-outline-danger ml-2"
                    type="button"
                    onClick={() => this.resetPhoto()}
                  >
                    Reset Gambar
                  </button>
                </div>
              </div>
              <div className="col-lg-4 my-auto">
                <h5>Pilih Gambar Dari File : </h5>
                <div class="input-group mt-2">
                  <div class="input-group-prepend">
                    <button
                      class="btn btn-danger"
                      type="button"
                      onClick={() => this.props.dispatch(setCameraURI(""))}
                    >
                      Hapus
                    </button>
                  </div>
                  <div class="custom-file">
                    <input
                      className="custom-file-input"
                      type="file"
                      name="avatar"
                      id="file"
                      accept=".jpef, .png, .jpg"
                      onChange={(data) => this.handleUpload(data)}
                      src={this.state.image}
                    />
                    <label class="custom-file-label" for="inputGroupFile03">
                      Choose file
                    </label>
                  </div>
                </div>
              </div>
            </div>
          </div>
          <div className="col-lg-12 col-md-12 mt-3">
            <div className="text-right">
              <button
                type="submit"
                className="btn btn-info"
                disabled={this.props.onSend}
              >
                {this.props.onSend ? (
                  <span
                    className="spinner-border spinner-border-sm mr-3"
                    aria-hidden="true"
                  ></span>
                ) : null}
                Selesai
                <i class="fa fa-paper-plane ml-3" aria-hidden="true"></i>
              </button>
            </div>
          </div>
        </div>
      </form>
    );
  }
}

FormKodeBarangAksesorisEdit = reduxForm({
  form: "KodeBarangEdit",
  enableReinitialize: true,
  validate: validationRosok,
})(FormKodeBarangAksesorisEdit);
export default connect(maptoState, null)(FormKodeBarangAksesorisEdit);
