import React, { Component } from "react";
import BootstrapTable from "react-bootstrap-table-next";
import ToolkitProvider, {
  Search,
  CSVExport,
} from "react-bootstrap-table2-toolkit";

import FormKodeBarang from "./Component/FormKodeBarang";
import { connect } from "react-redux";
import notrespond from "../Images/500.svg";
import Swal from "sweetalert2";
import { Link } from "react-router-dom";
import FormKodeBarangEdit from "./Component/FormKodeBarangEdit";
import {
  getListAksesoris,
  getListBarangMaster,
} from "../actions/kodeBarangAction";
import { reset } from "redux-form";
import Skeleton from "react-loading-skeleton";
import paginationFactory from "react-bootstrap-table2-paginator";
import { AxiosMasterDelete, AxiosMasterPost, AxiosMasterPut } from "../Axios";
import { berhasil, gagal, LogData, reactive } from "../Alert";
import { hideModal, showModal } from "../actions/ModalAction";
import ModalMasterData from "./Component/ModalMasterData";
import HeaderCard from "../HeaderCard";
import TourGuide from "../TourGuide";
import { finishSend, progressSend } from "../actions/LogicAction";
import ReactTooltip from "react-tooltip";
import { formatGram2, PlaceHolder } from "../helper";
import FormKodeBarangAksesoris from "./Component/FormKodeBarangAksesoris";
import FormKodeBarangAksesorisEdit from "./Component/FormKodeBarangAksesorisEdit";
import { editModel } from "../actions/barangAction";

const { SearchBar } = Search;
const { ExportCSVButton } = CSVExport;

function hapus(dispatch, kode) {
  Swal.fire({
    title: "Anda Yakin ?",
    text: "Anda Akan Menghapus Data Ini ?",
    icon: "warning",
    showCancelButton: true,
    confirmButtonColor: "#3085d6",
    cancelButtonColor: "#d33",
    confirmButtonText: "Yes, delete it!",
  }).then((result) => {
    if (result.isConfirmed) {
      AxiosMasterDelete("jenis/delete/by-kode-jenis/" + kode)
        .then((res) =>
          berhasil("Data Berhasil Dihapus!").then(() =>
            dispatch(getListBarangMaster())
          )
        )
        .catch((err) => gagal("Data Gagal Dihapus!"));
    }
  });
}
const maptoState = (state) => {
  return {
    KodeBarangList: state.provinsi.kodeBarang,
    alert: state.provinsi.alert,
    error: state.provinsi.error,
    cameraURI: state.provinsi.cameraURI,
    ListAksesoris: state.provinsi.ListAksesoris,
  };
};
class KodeBarang extends Component {
  constructor(props) {
    super(props);
    this.state = {
      KodeBarang: [],
      progress: 0,
      response: "",
      display: "none",
      columns: [
        {
          dataField: "kode_jenis",
          text: "Kode Model",
          headerStyle: () => {
            return { width: "10%" };
          },
        },
        {
          dataField: "nama_jenis",
          text: "Nama Model",
          headerStyle: () => {
            return { width: "20%" };
          },
        },
        {
          dataField: "kode_kategori",
          text: "Kode Kategori",
          headerStyle: () => {
            return { width: "10%" };
          },
        },
        {
          dataField: "kadar_cetak",
          text: "Cap Kode",
          align: "right",
          headerAlign: "right",
          formatter: (data) => formatGram2(data),
          headerStyle: () => {
            return { width: "8%" };
          },
        },
        {
          dataField: "kadar_beli",
          text: "Kode Awal",
          align: "right",
          formatter: (data) => formatGram2(data),
          headerAlign: "right",
          headerStyle: () => {
            return { width: "8%" };
          },
        },
        // {
        //   dataField: "kadar_jual",
        //   text: "Kode Tukar",
        //   headerStyle: () => {
        //     return { width: "8%" };
        //   },
        // },
        {
          dataField: "kode_barcode",
          text: "Kode Barcode",
          headerStyle: () => {
            return { width: "10%" };
          },
        },
        {
          dataField: "image",
          text: "Image",
          formatter: (cell, row, rowIndex, extraData) => (
            <div className=" row justify-content-center">
              <img
                src={row.image}
                alt="Gambar Model"
                width="100px"
                height="100px"
              />
            </div>
          ),
        },
        {
          dataField: "link",
          text: "Aksi",
          csvExport: false,
          formatter: (rowContent, row) => {
            return (
              <div className="row justify-content-center my-auto">
                <button
                  data-tip
                  data-for="hapus"
                  className="btn btn-danger mr-2"
                  onClick={() => hapus(this.props.dispatch, row.kode_jenis)}
                  data-tut="reactour_3"
                >
                  <i className="fa fa-trash"></i>
                  <ReactTooltip id="hapus" type="dark" effect="solid">
                    <span>Hapus</span>
                  </ReactTooltip>
                </button>
                <button
                  data-tip
                  data-for="edit"
                  data-tut="reactour_4"
                  className="btn btn-warning"
                  onClick={() =>
                    this.props.dispatch(
                      editModel(
                        row.kode_jenis,
                        row.kode_kategori,
                        row.kode_kelompok,
                        row.nama_jenis,
                        row.kadar_beli,
                        row.kadar_jual,
                        row.kadar_cetak,
                        row.image,
                        row.kode_intern,
                        row.kode_barcode,
                        this.editModal()
                      )
                    )
                  }
                >
                  <i className="fa fa-edit"></i>
                  <ReactTooltip id="edit" type="dark" effect="solid">
                    <span>Edit</span>
                  </ReactTooltip>
                </button>
              </div>
            );
          },
        },
      ],
      columnsAksesoris: [
        {
          dataField: "kode_jenis",
          text: "Kode Model",
          headerStyle: () => {
            return { width: "10%" };
          },
        },
        {
          dataField: "nama_jenis",
          text: "Nama Model",
          headerStyle: () => {
            return { width: "20%" };
          },
        },
        {
          dataField: "kode_kategori",
          text: "Kode Kategori",
          headerStyle: () => {
            return { width: "10%" };
          },
        },
        {
          dataField: "harga_beli",
          text: "Harga Beli",
          headerStyle: () => {
            return { width: "10%" };
          },
          align: "right",
          headerAlign: "right",
        },
        {
          dataField: "harga_jual",
          text: "Harga Jual",
          headerStyle: () => {
            return { width: "10%" };
          },
          align: "right",
          headerAlign: "right",
        },
        // {
        //   dataField: "kadar_jual",
        //   text: "Kode Tukar",
        //   headerStyle: () => {
        //     return { width: "8%" };
        //   },
        // },
        {
          dataField: "kode_barcode",
          text: "Kode Barcode",
          headerStyle: () => {
            return { width: "10%" };
          },
        },
        {
          dataField: "image",
          text: "Image",
          formatter: (cell, row, rowIndex, extraData) => (
            <div className=" row justify-content-center">
              <img
                src={row.image}
                alt="Gambar Model"
                width="100px"
                height="100px"
              />
            </div>
          ),
        },
        {
          dataField: "link",
          text: "Aksi",
          csvExport: false,
          formatter: (rowContent, row) => {
            return (
              <div className="row justify-content-center my-auto">
                <button
                  data-tip
                  data-for="hapus"
                  className="btn btn-danger mr-2"
                  onClick={() => hapus(this.props.dispatch, row.kode_jenis)}
                  data-tut="reactour_3"
                >
                  <i className="fa fa-trash"></i>
                  <ReactTooltip id="hapus" type="dark" effect="solid">
                    <span>Hapus</span>
                  </ReactTooltip>
                </button>
                <button
                  data-tip
                  data-for="edit"
                  data-tut="reactour_4"
                  className="btn btn-warning"
                  onClick={() =>
                    this.props.dispatch(
                      editModel(
                        row.kode_jenis,
                        row.kode_kategori,
                        row.kode_kelompok,
                        row.nama_jenis,
                        row.kadar_beli,
                        row.kadar_jual,
                        row.kadar_cetak,
                        row.image,
                        row.kode_intern,
                        row.kode_barcode,
                        row.harga_jual,
                        row.harga_beli,
                        this.editModalAksesoris()
                      )
                    )
                  }
                >
                  <i className="fa fa-edit"></i>
                  <ReactTooltip id="edit" type="dark" effect="solid">
                    <span>Edit</span>
                  </ReactTooltip>
                </button>
              </div>
            );
          },
        },
      ],
    };
  }

  componentDidMount() {
    this.props.dispatch(getListBarangMaster());
    this.props.dispatch(getListAksesoris());
  }

  handleSubit(hasil, jenis) {
    let data = "";
    let dataEdit = "";
    if (jenis === "NON-AKSESORIS") {
      data = {
        kode_kategori: hasil.kodeKategori,
        kode_kelompok: "PERHIASAN",
        image:
          this.props.cameraURI.length === 0
            ? PlaceHolder
            : this.props.cameraURI,
        kode_intern: hasil.kode_intern || "-",
        kode_barcode: hasil.kodeJenis + "|" + hasil.kadarCetak + "|" + 0,
        kode_jenis: hasil.kodeJenis,
        kadar_cetak: hasil.kadarCetak.toString(),
        kadar_beli: hasil.kadarBeli || 0,
        nama_jenis: hasil.namaJenis,
        kadar_jual: 0,
        harga_beli: 0,
        harga_jual: 0,
      };
      dataEdit = {
        kode_kategori: hasil.kodeKategori,
        kode_kelompok: "PERHIASAN",
        image: this.props.cameraURI.length === 0 ? "-" : this.props.cameraURI,
        kode_intern: hasil.kode_intern || "-",
        kode_barcode:
          hasil.kodeJenis +
          "|" +
          hasil.namaJenis +
          "|" +
          hasil.kadarCetak +
          "|" +
          0,
        kadar_cetak: hasil.kadarCetak.toString(),
        kadar_beli: hasil.kadarBeli || 0,
        nama_jenis: hasil.namaJenis,
        kadar_jual: 0,
        harga_beli: 0,
        harga_jual: 0,
      };
    } else {
      data = {
        kode_kategori: hasil.kodeKategori,
        kode_kelompok: "AKSESORIS",
        kode_intern: hasil.kode_intern || "-",
        kode_barcode:
          hasil.kodeJenis + "|" + hasil.harga_beli + "|" + hasil.harga_jual,
        kode_jenis: hasil.kodeJenis,
        kadar_cetak: "0",
        kadar_beli: 0,
        nama_jenis: hasil.namaJenis,
        kadar_jual: 0,
        harga_beli: hasil.harga_beli || 0,
        harga_jual: hasil.harga_jual || 0,
        image:
          this.props.cameraURI.length === 0
            ? PlaceHolder
            : this.props.cameraURI,
      };
      dataEdit = {
        kode_kategori: hasil.kodeKategori,
        kode_kelompok: "AKSESORIS",
        image: this.props.cameraURI.length === 0 ? "-" : this.props.cameraURI,
        kode_intern: hasil.kode_intern || "-",
        kode_barcode:
          hasil.kodeJenis + "|" + hasil.harga_beli + "|" + hasil.harga_jual,
        kadar_cetak: "0",
        kadar_beli: 0,
        nama_jenis: hasil.namaJenis,
        kadar_jual: 0,
        harga_beli: hasil.harga_beli || 0,
        harga_jual: hasil.harga_jual || 0,
      };
    }
    console.log(JSON.stringify(data));
    this.props.dispatch(progressSend());
    let counter = setInterval(() => {
      this.setState({
        progress: this.state.progress + 1,
      });
      console.log(this.state.progress);
    }, 500);
    AxiosMasterPost("jenis/add", data)
      .then(() => this.props.dispatch(finishSend()))
      .then(() => this.props.dispatch(hideModal()))
      .then((res) =>
        berhasil("Data Berhasil Ditambah!")
          .then(() => finishSend())
          .then(() => clearInterval(counter))
          .then(() => LogData("Data Berhasil Ditambah Di Kode Model", data))
          .then(() => this.props.dispatch(getListBarangMaster()))
          .then(() => this.props.dispatch(getListAksesoris()))
          .then(() => this.props.dispatch(reset("KodeBarang")))
      )
      .catch((err) =>
        this.handleReactive(
          err,
          hasil.kodeJenis,
          dataEdit,
          this.props.dispatch(finishSend()),
          this.props.dispatch(hideModal())
        )
      );
  }
  handleEdit(hasil) {
    let data = {
      kode_kategori: hasil.kodeKategori,
      kode_kelompok: "PERHIASAN",
      kadar_cetak: hasil.kadarCetak.toString(),
      kadar_beli: hasil.kadarBeli,
      nama_jenis: hasil.namaJenis,
      kadar_jual: 0,
      harga_beli: hasil.harga_beli || 0,
      harga_jual: hasil.harga_jual || 0,
      image:
        this.props.cameraURI.length === 0 ? PlaceHolder : this.props.cameraURI,
      kode_intern: hasil.kode_intern || "-",
      kode_barcode: hasil.kodeJenis + "|" + hasil.kadarCetak + "|" + 0,
    };

    this.props.dispatch(progressSend());
    let counter = setInterval(() => {
      if (this.state.progress === 90) {
        clearInterval(counter);
      }
      this.setState({
        progress: this.state.progress + 2,
      });
      console.log(this.state.progress);
    }, 50);
    // console.log(data);
    // return false;
    AxiosMasterPut(`jenis/edit/by-kode-jenis/${hasil.kodeJenis}`, data)
      .then(() => clearInterval(counter))
      .then(() => this.props.dispatch(finishSend()))
      .then(() => this.props.dispatch(hideModal()))
      .then((res) =>
        berhasil("Data Berhasil Diubah!").then(() =>
          this.props.dispatch(getListBarangMaster())
        )
      )
      .catch((err) =>
        gagal("Data Gagal Diubah!")
          .then(() => this.props.dispatch(hideModal()))
          .then(() => finishSend())
      );
  }
  handleEditAksesoris(hasil) {
    let data = {
      kode_kategori: hasil.kodeKategori,
      kode_kelompok: "AKSESORIS",
      kadar_cetak: hasil.kadarCetak.toString(),
      kadar_beli: hasil.kadarBeli,
      nama_jenis: hasil.namaJenis,
      kadar_jual: 0,
      harga_beli: hasil.harga_beli || 0,
      harga_jual: hasil.harga_jual || 0,
      image:
        this.props.cameraURI.length === 0 ? PlaceHolder : this.props.cameraURI,
      kode_intern: hasil.kode_intern || "-",
      kode_barcode: hasil.kodeJenis + "|" + hasil.kadarCetak + "|" + 0,
    };
    // console.log(data);
    // return false;
    this.props.dispatch(progressSend());
    AxiosMasterPut(`jenis/edit/by-kode-jenis/${hasil.kodeJenis}`, data)
      .then(() => this.props.dispatch(finishSend()))
      .then(() => this.props.dispatch(hideModal()))
      .then((res) =>
        berhasil("Data Berhasil Diubah!").then(() =>
          this.props.dispatch(getListBarangMaster())
        )
      )
      .catch((err) =>
        gagal(err?.response.data)
          .then(() => this.props.dispatch(hideModal()))
          .then(() => finishSend())
      );
  }
  tambahModal() {
    this.props.dispatch(showModal());
    this.setState({ isEdit: false, jenis: "NON-AKSESORIS" });
  }
  tambahModalAksesoris() {
    this.props.dispatch(showModal());
    this.setState({
      isEdit: false,
    });
  }
  editModal() {
    this.props.dispatch(showModal());
    this.setState({ isEdit: true, jenis: "NON-AKSESORIS" });
  }
  editModalAksesoris() {
    this.props.dispatch(showModal());
    this.setState({ isEdit: true, jenis: "AKSESORIS" });
  }
  handleReactive(err, kode, data) {
    this.props.dispatch(hideModal());
    let error = err.response.data;
    let check = error.includes("Deleted");
    check
      ? reactive(
          err,
          kode,
          "jenis/reactive/by-kode-jenis/",
          data,
          "jenis/edit/by-kode-jenis/"
        ).then(() => this.props.dispatch(getListBarangMaster()))
      : gagal("Data Gagal Ditambahkan");
  }
  render() {
    return (
      <div className="container-fluid" style={{ color: "black" }}>
        <div id="notif_alert" className={this.props.alert} role="alert">
          <button
            type="button"
            className="close"
            data-dismiss="alert"
            aria-label="Close"
          >
            <span aria-hidden="true"></span>
          </button>
          <strong>Error - </strong> Tidak Bisa Mengambil Data
        </div>
        <ol className="breadcrumb mb-2">
          <Link to="/KlasifikasiBarang">
            <button
              type="button"
              className="btn btn-secondary "
              data-tut="reactour_5"
            >
              <i className="fa fa-angle-left mr-3" aria-hidden="true"></i> Back
            </button>
          </Link>
        </ol>
        <TourGuide
          tourConfig={[
            `Untuk Menambahkan Data Kelompok, Tekan Tombol ini Dan Isi kolom yang disediakan`,
            `Ini adalah Data Kelompok yang sudah tersimpan`,
            `Ini adalah tombol untuk menghapus data pada baris yang dipilih`,
            `Ini adalah tombol untuk mengubah data pada baris yang dipilih`,
            `Ini adalah tombol navigasi untuk kembali`,
          ]}
        />
        <div className="row">
          <div className="col-12">
            <div className="card">
              <HeaderCard title="Data Model" />
              <div className="card-body">
                <ModalMasterData
                  content={
                    this.state.jenis === "NON-AKSESORIS" ? (
                      this.state.isEdit ? (
                        <FormKodeBarangEdit
                          onSubmit={(data) =>
                            this.handleEdit(data, "NON-AKSESORIS")
                          }
                          onSend={this.props.onSend}
                          percentage={this.state.progress}
                        />
                      ) : (
                        <FormKodeBarang
                          onSubmit={(data) =>
                            this.handleSubit(data, "NON-AKSESORIS")
                          }
                          onSend={this.props.onSend}
                        />
                      )
                    ) : this.state.isEdit ? ( //AKSESORIS
                      <FormKodeBarangAksesorisEdit
                        onSubmit={(data) =>
                          this.handleEditAksesoris(data, "AKSESORIS")
                        }
                        onSend={this.props.onSend}
                      />
                    ) : (
                      <FormKodeBarangAksesoris
                        onSubmit={(data) => this.handleSubit(data, "AKSESORIS")}
                        onSend={this.props.onSend}
                      />
                    )
                  }
                  title={
                    this.state.isEdit
                      ? "Edit Data Kode Model"
                      : "Tambah data Kode Model"
                  }
                />
                <div className="col-lg-12 col-md-12">
                  <ul className="nav nav-pills bg-nav-pills nav-justified mb-3">
                    <li className="nav-item">
                      <a
                        href="#non-aksesoris-tab"
                        data-toggle="tab"
                        aria-expanded="false"
                        className="nav-link active"
                        onClick={() =>
                          this.setState({
                            jenis: "NON-AKSESORIS",
                          })
                        }
                      >
                        <i className="mdi mdi-home-variant d-lg-none d-block mr-1" />
                        <span className="d-none d-lg-block">Non Aksesoris</span>
                      </a>
                    </li>
                    <li className="nav-item">
                      <a
                        href="#aksesoris-tab"
                        data-toggle="tab"
                        aria-expanded="true"
                        className="nav-link "
                        onClick={() =>
                          this.setState({
                            jenis: "AKSESORIS",
                          })
                        }
                      >
                        <i className="mdi mdi-account-circle d-lg-none d-block mr-1" />
                        <span className="d-none d-lg-block">Aksesoris</span>
                      </a>
                    </li>
                  </ul>
                  <div className="tab-content">
                    <div
                      className="tab-pane show active"
                      id="non-aksesoris-tab"
                    >
                      <div data-tut="reactour_2">
                        <div className="col-lg-12 col-md-12">
                          <div className="text-right">
                            <button
                              name="simpan"
                              id="simpan"
                              type="button"
                              className="btn btn-info"
                              onClick={() => this.tambahModal()}
                              data-tut="reactour_1"
                            >
                              Tambah Data
                              <i className="fa fa-plus ml-3"></i>
                            </button>
                          </div>
                        </div>
                        {this.props.KodeBarangList ? (
                          <ToolkitProvider
                            keyField="id"
                            data={this.props.KodeBarangList.filter(
                              (data) => data.kode_kelompok === "PERHIASAN"
                            )}
                            columns={this.state.columns}
                            search
                          >
                            {(props) => (
                              <div className="form-group">
                                <div className="text-right mt-5 mb-3">
                                  <SearchBar
                                    {...props.searchProps}
                                    className="form-control"
                                    placeholder="Pencarian"
                                    style={{ margin: 0, top: "50%" }}
                                  />
                                </div>
                                <BootstrapTable
                                  {...props.baseProps}
                                  striped
                                  hover
                                  condensed
                                  bordered={true}
                                  headerClasses="header-table"
                                  pagination={paginationFactory()}
                                />
                                <ExportCSVButton
                                  {...props.csvProps}
                                  className="btn btn-primary"
                                >
                                  Export CSV
                                </ExportCSVButton>
                              </div>
                            )}
                          </ToolkitProvider>
                        ) : (
                          <div>
                            {this.props.error ? (
                              <div className="text-center">
                                <img
                                  src={notrespond}
                                  style={{ width: "30%" }}
                                  alt="NOT RESPOND"
                                />
                                <br />
                                <h4>Request API Gagal, Silahkan Refresh</h4>
                              </div>
                            ) : (
                              <Skeleton width={"100%"} height={180} />
                            )}
                          </div>
                        )}
                      </div>
                    </div>
                    <div className="tab-pane " id="aksesoris-tab">
                      <div data-tut="reactour_2">
                        <div className="col-lg-12 col-md-12">
                          <div className="text-right">
                            <button
                              name="simpan"
                              id="simpan"
                              type="button"
                              className="btn btn-info"
                              onClick={() => this.tambahModalAksesoris()}
                              data-tut="reactour_1"
                            >
                              Tambah Data
                              <i className="fa fa-plus ml-3"></i>
                            </button>
                          </div>
                        </div>
                        {this.props.KodeBarangList ? (
                          <ToolkitProvider
                            keyField="id"
                            data={this.props.ListAksesoris}
                            columns={this.state.columnsAksesoris}
                            search
                          >
                            {(props) => (
                              <div className="form-group">
                                <div className="text-right mt-5 mb-3">
                                  <SearchBar
                                    {...props.searchProps}
                                    className="form-control"
                                    placeholder="Pencarian"
                                    style={{ margin: 0, top: "50%" }}
                                  />
                                </div>
                                <BootstrapTable
                                  {...props.baseProps}
                                  striped
                                  hover
                                  condensed
                                  bordered={true}
                                  headerClasses="header-table"
                                  pagination={paginationFactory()}
                                />
                                <ExportCSVButton
                                  {...props.csvProps}
                                  className="btn btn-primary"
                                >
                                  Export CSV
                                </ExportCSVButton>
                              </div>
                            )}
                          </ToolkitProvider>
                        ) : (
                          <div>
                            {this.props.error ? (
                              <div className="text-center">
                                <img
                                  src={notrespond}
                                  style={{ width: "30%" }}
                                  alt="NOT RESPOND"
                                />
                                <br />
                                <h4>Request API Gagal, Silahkan Refresh</h4>
                              </div>
                            ) : (
                              <Skeleton width={"100%"} height={180} />
                            )}
                          </div>
                        )}
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default connect(maptoState, null)(KodeBarang);
